@import "../styles/global";

@keyframes expand {
  0% { transform: scale(1); }
  100% { transform: scale(1.1); }
}

@keyframes contract {
  0% { transform: scale(1.1); }
  100% { transform: scale(1); }
}

.skill {
		@include centerY;
				
		margin: 3rem;
		padding: 6rem;
		border-radius: 50%;
		color: var(--color-button-1);		

		&__icon {
			font-size: 8rem;
			
		}

		&__name {
			font-size: 3rem;
			text-transform: uppercase;
			color: var(--color-button-1);
		}

		&__desc {
			@include centerX;
			width: 16rem;
			text-align: center;
			text-transform: uppercase;
			padding: .5rem 0;
			font-size: 1.8rem;
			
		}

		&__exp {
			animation: expand 1s forwards;

		}

		&__contr {
			animation: contract 1s forwards;
			background-color: var(--color-secondary-light);

		}

		&--1 {
			height: 19rem;
			width: 19rem;
			background-color: rgba(#19424e, .2);
			
		}

		&--2 {
			height: 20rem;
			width: 20rem;
			background-color: rgba(#19424e, .3);
			
		}

		&--3 {
			height: 21rem;
			width: 21rem;
			background-color: rgba(#19424e, .4);
			
		}

		&--4 {
			height: 22rem;
			width: 22rem;
			background-color: rgba(#19424e, .6);	
					
		}

		&--5 {
			height: 23rem;
			width: 23rem;
			background-color: rgba(#19424e, .7);	
		
		}

		&--6 {
			height: 24rem;
			width: 24rem;
			background-color: rgba(#19424e, .8);	
				
		}
	}

@media only screen and (max-width: 720px) {

	.skill {

			height: calc(6rem + 2vmin);
			width: calc(6rem + 2vmin);
			margin: 1rem;
			
			&__icon {
				font-size: calc(3rem + 2vmin);
			}

			&__name {
				font-size: calc(1rem + 2vmin);
				padding: 0;
			}

			&__desc {
				padding: 0;
				font-size: calc(.8rem + 1vmin);
				width: calc(4rem + 2vmin);
			}
		}
}		