@import "../styles/global";

.job {
	margin: 8rem;
	padding: 2rem;
	border-radius: 6px;	
	overflow: hidden;
 	background-color: rgba(#FBF7F0, .4);
	transform: skewY(-2deg);	

	& > * {	
		text-transform: uppercase;
		padding: 1rem 3rem;
		transform: skewY(2deg);
	}

	&__details {
		display: flex;
		flex-direction: row;
		align-items: center;
		padding-top: 3rem;
	}
	

	&__title {
		font-size: 2.3rem;
		color: var(--color-primary);
		text-align: right;
		margin-left: 3rem;
		padding: 0rem 0rem;
	} 

	&__company {
		font-size: 1.8rem;			
		color: var(--color-j-1);
		margin-right: auto;	
	}

	&__dates {
		font-size: 1.5rem;
		color: var(--color-teal-4);
	}

	&__desc {
		font-size: 1.1rem;
		color: var(--color-primary);
		max-width: 80rem;

		& ul {
			& > * {
				padding: .6rem 0;
			}
		} 
	}

	&__school {
		font-size: 1.6rem;
		margin-right: auto; 
	}
}

@media only screen and (max-width: 720px) {
	.job {
		margin: 3rem;
		
		& > * {	
			padding: 1rem;	
		}

		&__details {
			display: flex;
			flex-direction: column;
			align-items: flex-start;
		}

		&__title {
			margin: .5rem 0;
			font-size: calc(1.5rem + 1vmin);
		}

		&__company {
			font-size: calc(1rem + 1vmin);		
		}

		&__dates {
			font-size: calc(.9rem + 1vmin);
		}

		&__desc {

			font-size: calc(.6rem + 1.2vmin);	

			& ul {
				& > * {
					padding: .3rem 0;
				}
			} 
		}
	}
}