@import "../../styles/global";

.nav {	

	& > * {		
		margin: 0 2rem;
		display: inline-block;
		text-align: center;
		text-transform: uppercase;
		text-decoration: none;
		font-size: 1.8rem;
		padding: 1rem 1rem 2rem;
	}

	&__link	 {
	
		color: var(--color-teal-0);
		transition: all .2s;

		&:hover {	
			transform: scale(1.05);
		}
	}

	&__active {
		color: var(--color-j-3);	
	}
}

@media only screen and (max-width: 768px) {

  	.nav {

  		& > * {		
			margin: 0;
			font-size: calc(.7rem + .5vmin);
			padding: 1rem .5rem 1.5rem;
		}

		&__link	 {
			
			color: var(--color-teal-0);
			transition: all .2s;

			&:hover {
				color: var(--color-j-3);	
				transform: scale(1.05);
			}
		}

		&__active {	
			font-size: calc(1rem + .5vmin);
			color: var(--color-j-3);
		}
 	}
}