:root {
    //colors
	--color-primary: #181D31;

	--color-secondary-lightest: #FBF7F0;
	--color-secondary-lighter: #f7f3e8;
	--color-secondary-light: #F0E9D2;
	--color-secondary-dark: #E6DDC4;


	--color-teal-0: #D6EBE2;
	--color-teal-1: #C8E3D4;
	--color-teal-1_5: #B6D2C5;
	--color-teal-2: #98B6AC;
	--color-teal-2_5: #80A098;
	--color-teal-3: #678983;
	--color-teal-4: #536976;

	--color-button-2: #FFC900;

	--color-button-1: #FED049;

	--color-insert: #E4F2F0;

	--color-j-1: #19424e;
	--color-j-2: #315564;
	--color-j-3: #ebe4a5;
	--color-j-4: #688274;
	--color-j-5: #603540;
	--color-j-6: #c4462b;

	//shadows
	--shadow-dark: 0 2rem 6rem rgba(0, 0, 0, .3);
	--shadow-light: 1rem 2rem 5rem rgba(0,0,0,.06);
}

* {
	margin: 0;
	padding: 0;
}

*,
*::before,
*::after {
	box-sizing: inherit;

}

html {
	box-sizing: border-box;
	font-size: 62.5%;
	

}

body {
	font-family: 'Noto Sans', sans-serif;
	font-weight: 400; 
	
}

html, body, #root {
    height: 100%;
}

