@import "../styles/global";

.info {
		
	&__name,
	&__contact,
	&__job,
	&__form {
		@include centerY;
		padding: 0 2rem;
		text-align: center;
		margin: 0 3rem;
	}

	&__label {
		font-size: 2rem;
		color: var(--color-insert);
	}

	&__input {
		font-size: 2rem;
		padding: 2rem 3rem;
		margin: 1rem 0;
		border-radius: 2rem;
		outline: none;
		border: 0;
		color: var(--color-teal-4);
		background-color: rgba(#E4F2F0, .4);
		text-align: center;

		&--error {
			font-size: 2rem;
		padding: 2rem 3rem;
		margin: 1rem 0;
		border-radius: 2rem;
		outline: none;
		border: 0;
		color: var(--color-teal-4);
		text-align: center;

			background-color: rgba(red, .4);
		}

		&::placeholder {
			color: var(--color-teal-4);
		}
	}

	&__name {
		font-size: 6rem;
		margin: 1rem;
		text-transform: uppercase;
	}

	&__job {
		margin: 0 1rem 3rem;
		text-transform: uppercase;
	}

	&__contact {
		
		font-size: 3rem;
		display: flex;

		& > * {
			padding: 1rem 0;
			text-decoration: none;	
			color: var(--color-primary);
		}
	}

	&__tooltip {
		background-color: var(--color-teal-1) !important;
		color: var(--color-primary) !important;
		font-size: 2rem !important;
		padding: 2rem !important;
		border-radius: 6px !important;
	}
}	

@media only screen and (max-width: 720px) {
	.info {
		&__name {
			font-size: calc(2rem + 2vmin);
		}

		&__job {
			font-size: calc(1.5rem + 1vmin);
		}

		&__contact, 
		&__form,
		&__input {
			font-size: calc(1rem + 2vmin);
		}

		&__label {
			font-size: calc(1rem + 1vmin);
		}
	}
}