@import "../styles/global";

@keyframes expand {
  0% { transform: scale(1); }
  100% { transform: scale(1.1); }
}

@keyframes contract {
  0% { transform: scale(1.1); }
  100% { transform: scale(1); }
}

.language {
		@include centerY;	
		margin: 3rem 6rem;
		padding: 6rem;
		border-radius: 50%;
			background-color: rgba(#19424e, .2);
			color: var(--color-button-1);
		

		&__name {
			font-size: 3rem;
			text-transform: uppercase;
			padding: 1rem;
		}

		&__desc {
			@include centerX;
			width: 16rem;
			text-align: center;
			text-transform: uppercase;
			padding: .5rem 0;
			font-size: 1.8rem;
			
		}

		&__exp {
			animation: expand 1s forwards;
			border-radius: 50%;
			background-color: rgba(#19424e, .5);
			color: var(--color-button-1);
		}

		&__contr {
			animation: contract 1s forwards;
		}

		&--1 {
			height: 20rem;
			width: 20rem;
		}

		&--2 {
			height: 22rem;
			width: 22rem;	
		}

		&--3 {
			height: 24rem;
			width: 24rem;			
		}

		&--4 {
			height: 26rem;
			width: 26rem;				
		}

		&--5 {
			height: 28rem;
			width: 28rem;
		}

		&--6 {
			height: 30rem;
			width: 30rem;	
		}
	}

@media only screen and (max-width: 720px) {

.language {
			margin: 2rem;
		  padding: 3rem;
			height: calc(12rem + 3vmin);
			width: calc(12rem + 3vmin);
			box-shadow: .2rem .2rem 2rem .8rem rgba(#536976, .2);

			&__name {
				font-size: calc(1rem + 2vmin);
			}

			&__desc {
				
				font-size: calc(.8rem + 1vmin);
				width: calc(4rem + 2vmin);
			}
		}
}		