@import "../styles/global";

.edu {
	margin: 8rem;
	padding: 3rem 1rem;
	border-radius: 6px;	
	overflow: hidden;
 	background-color: rgba(#FBF7F0, .4);
	transform: skewY(-2deg);	

	& > * {	
		text-transform: uppercase;
		padding: 1rem 3rem;
		transform: skewY(2deg);
	}

	&__school	{
		font-size: 2.1rem;
		color: var(--color-j-1);
	}

	&__dates{
		font-size: 1.5rem;
		color: var(--color-teal-4);
	} 

	&__degree{
		font-size: 1.4rem;	
		color: var(--color-primary);
	}
}

@media only screen and (max-width: 720px) {
	.edu {
		margin: 3rem;
		padding: 2rem;
			& > * {	
				padding: 1rem;	
			}

		&__school	{
			font-size: calc(1.4rem + 1vmin);
		} 

		&__dates{
			font-size: calc(1.1rem + 1vmin);
		}

		&__degree	{
			font-size: calc(1rem + 1vmin);
		} 
	}
}