@import "../../styles/global";

.btn {
	display: block;
	float: left;
	margin: 0 auto;	
	font-size: 1.5rem;
	text-align: center;
	text-transform: uppercase;
	text-decoration: none;
	border-radius: 36px;
	border: none;
	background-color: var(--color-button-1);
	color: var(--color-teal-4);
	padding: 2rem 3rem;
	position: relative;
	overflow: hidden;
	cursor: pointer;
	margin: 2rem;
	transition: all .2s;

	&:hover {
		transform: scale(1.1);
	}

	&__icon {
		margin-left: 2rem;
		color: var(--color-teal-4);
	}

	&--back {
		color: var(--color-button-1);
		background-color: var(--color-teal-4);
		padding: .5rem 1rem;
		margin: 1rem 1rem;
	}	

	&--next {
		color: var(--color-teal-4);
		background-color: var(--color-button-1);
		padding: .5rem 1rem;
		margin: 1rem 1rem;
	}

	&--save {
		color: var(--color-secondary-lighter);
		padding: 2rem 4rem;
		background-color: rgba(#19424e, .2);
		box-shadow: var(--shadow-light);
	}
} 